// В этом файле должны быть стили для БЭМ-блока page-head, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.page-head {

  $block-name:                &; // #{$block-name}__element


  &__title {
    font-weight: 600;
    font-size: calc(28px + (68 - 14) * ((100vw - 300px) / (1600 - 300)));
  }

  &__subtitle {
    font-weight: 300;
    font-size: calc(15px + (26 - 14) * ((100vw - 300px) / (1600 - 300)));
  }

  &__content {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 15px;
  }

  &__heading {
    text-align: center;
    color: #fff;
    padding: 24px 0 24px 0;
    letter-spacing: 1px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    max-width: 700px;
    word-wrap: break-word;
  }
}
