@mixin transition($in) {
  transition: $in;
}

@mixin transform($in) {
  transform: $in;
}

@mixin animation($in) {
  animation: $in;
}

.panel {

  $block-name: &; // #{$block-name}__element

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 15px 0 15px;
    flex-wrap: wrap;

    @media (min-width: $screen-md) {
      flex-wrap: nowrap;
      padding: 20px 25px;
    }
  }

  &__item {
    flex-wrap: wrap;

    @media (min-width: $screen-md) {
      width: auto;
    }

    &--left {
      text-align: left;
      flex: 1;
      order: 1;

      @media (min-width: $screen-md) {
        flex: 1;
      }
    }

    &--center {
      text-align: center;
      font-weight: 300;
      font-size: 14px;
      order: 3;
      margin-top: 20px;
      flex-basis: 100%;

      @media (min-width: $screen-md) {
        order: 2;
        flex: 2;
        font-size: 20px;
        margin-top: 0;
      }
    }

    &--right {
      text-align: right;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      order: 2;
      flex-basis: 50%;

      @media (min-width: $screen-md) {
        order: 3;
        flex: 1;
      }
    }
  }

  &__tel {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    color: #fff;
    position: relative;
    flex-shrink: 0;
    font-size: 13px;

    @media (min-width: $screen-md) {
      font-size: 14px;
    }

    &:first-of-type {
      margin-right: 5px;
    }

    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 0;
      height: 1px;
      background-color: #ff0000;
      @include transition(all 0.2s ease-in-out);
    }

    &:hover::after {
      width: 100%;
    }
  }

  &__email {
    color: #fff;
  }

  &__logo {
    text-transform: uppercase;
    font-size: 17px;
    font-weight: 400;
    display: inline-block;
    vertical-align: middle;
    background-color: #ff1119;
    padding: 10px;
    border-radius: 2px;
    white-space: nowrap;
    color: #fff;
    cursor: default;

    @media (min-width: $screen-md) {
      font-size: 24px;
    }
  }

  &__icon {
    width: 14px;
    height: 14px;
    fill: #fff;
    margin-right: 10px;

    @media (min-width: $screen-md) {
      width: 16px;
      height: 16px;
    }
  }
}
