
@mixin transition($in) {
  transition: $in;
}

@mixin transform($in) {
  transform: $in;
}

@mixin animation($in) {
  animation: $in;
}

$gray-lighter:                hsl(0, 0%, 80%) !default;
$gray-lightest:               hsl(0, 0%, 90%) !default;
$text-color:                  hsl(0, 0%, 10%) !default;
$line-height:                 1.375em;

$border-radius:               3px !default;

.effect-one {
  border-color: #fff;
  overflow: hidden;
  color: #fff;
  position: relative;

  &__item {
    background-color: #fff;
    width: 100%;
    height: 20px;
    position: absolute;
    left: -135px;

    @include transform(rotateZ(45deg));
  }

  &:hover {
    background-color: #ff0000;
    border-color: #fff;
    color: #fff;

    .effect-one__item {
      @include transition(all 0.3s ease);
      left: 135px;
    }
  }
}

.btn {

  $block-name:                &; // #{$block-name}__element

  box-sizing: border-box;
  display: inline-block;
  margin: 0;
  padding: 0.6em 2em;
  white-space: nowrap;
  vertical-align: middle;
  user-select: none;
  text-align: center;
  font-size: 1.2em;
  font-family: inherit;
  line-height: $line-height;
  text-decoration: none;
  color: #fff;
  border: 1px solid #68539c;
  background: rgba(57, 63, 84, 0.8);
  background-image: none;
  cursor: pointer;
  position: relative;
  transition: all 0.3s ease-in-out;
  text-transform: uppercase;

  &:hover,
  &:focus {
    text-decoration: none;
    color: #000;
    background-color: #fff;
  }

  &:active,
  &:focus {
    @include field-focus;
  }

  &--disabled,
  &:disabled {
    opacity: $opacity;
    cursor: not-allowed;
    color: $text-color;
    background-color: $gray-lightest;
    border-color: $gray-lighter;

    @at-root a.btn--disabled {
      pointer-events: none;
    }
  }

