$input-background: rgba(57, 63, 84, 0.8);
$input-text-inactive: #7881A1;
$input-text-active: #BFD2FF;

.feedback {

  $block-name: &; // #{$block-name}__element

  &__inner {
    max-width: 560px;
    margin-left: auto;
    margin-right: auto;
    padding: 0 15px;
  }

  &__input-wrap {
    position: relative;
    display: flex;
    flex-direction: row;
    width: 100%;
    max-width: 400px;
    margin: 0 auto;
    border-radius: 2px;
    background: $input-background;

    &::after {
      content: '';
      position: absolute;
      display: block;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 999;
      height: 2px;
      border-bottom-left-radius: 2px;
      border-bottom-right-radius: 2px;
      background-position: 0% 0%;
      background-image: linear-gradient(to right, #b294ff, #57e6e6, #feffb8, #57e6e6, #b294ff, #57e6e6);
      background-size: 500% auto;
      animation: gradient 3s linear infinite;
    }
  }

  &__input {
    flex-grow: 1;
    color: $input-text-active;
    font-size: 1.8rem;
    line-height: 2.4rem;
    vertical-align: middle;
    background-color: transparent;
    border: none;
    outline: none;
    font-family: $font-family;
    padding: 1.4rem 2rem 1.6rem;

    &::-webkit-input-placeholder {
      color: $input-text-inactive;
    }

    &::-moz-placeholder {
      color: $input-text-inactive;
    }

    &:-moz-placeholder {
      color: $input-text-inactive;
    }

    &:-ms-input-placeholder {
      color: $input-text-inactive;
    }

    &:focus {
      box-shadow: none;
    }
  }

  &__form-name {
    display: flex;
    font-size: 18px;
    line-height: 1.4;
    color: #fff;
    text-align: center;
    padding: 0;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 30px;
  }

  &__fieldset {
    border: none;
    margin: 0;
    padding: 0;
  }

  &__row {
    margin-bottom: 30px;
  }

  &__action {
    display: flex;
    justify-content: center;
  }

  &__help-text {
    color: rgba(255,255,255,.66);
    text-align: center;
    display: block;
    font-size: 10px;
  }
}

// gradient animation
@keyframes gradient {

  0% {
    background-position: 0 0;
  }

  100% {
    background-position: 100% 0;
  }
}


.parsley-errors-list {
  position: absolute;
  color: #ff8a8a;
  bottom: -21px;
}


input.parsley-error {
  color: #B94A48;
  background-color: transparent;
  border: none;
  border-left: 6px solid #B94A48;
}

input.parsley-success {
  color: #468847;
  background-color: transparent;
  border: none;
  border-left: 6px solid #468847;
}
