// В этом файле должны быть стили для БЭМ-блока slick, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority
.wrapper {
  max-width: 600px;
  margin: 0 auto;
}

.carousel {
  max-width: 600px;
  margin: 0 auto;
  opacity: 0;
  transition: opacity 0.3s ease-in;
  $block-name: &; // #{$block-name}__element

  &__item {
    padding: 4%;
    height: 250px;

    img {
      width: 100%;
      height: 250px;
      object-fit: cover;
    }
  }
}

.slick-arrow:hover {
  background-color: rgba(57, 63, 84, 0.8);
}

/* Arrows */
.slick-prev,
.slick-next {
  font-size: 0;
  line-height: 0;

  position: absolute;
  top: 50%;

  display: block;

  width: 20px;
  height: 20px;
  transform: translate(0, -50%);

  cursor: pointer;

  color: transparent;
  border: none;
  outline: none;
  background: transparent;
  background-size: 14px 14px;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 50%;
  padding: 15px;
}

.slick-next {
  background-image: svg-load('../blocks/slick/bg-img/next.svg');
}

.slick-prev {
  background-image: svg-load('../blocks/slick/bg-img/back.svg');
}

.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  color: transparent;
  outline: none;
}

.slick-prev:hover:before,
.slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before {
  opacity: 1;
}

.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
  opacity: 0.25;
}

.slick-prev {
  left: -30px;
}

[dir='rtl'] .slick-prev {
  right: -30px;
  left: auto;
}

.slick-next {
  right: -30px;
}

[dir='rtl'] .slick-next {
  right: auto;
  left: -30px;
}

.slick-lightbox-slick-img {
  width: 100%;
  object-fit: cover;
}
